import { Component } from '@angular/core';
import { ConfigFetcherService } from './services/config-fetcher/config-fetcher.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
    public lookupDictionary: any = {};
    public currentView: any = {};
    public config: any = {};
    public error: string = null;
    constructor(public configFetcher: ConfigFetcherService) {
        configFetcher.getPanelConfig().then(x => {
            this.config = x;
            try {
                var defaultView = x.views.filter(x => x.isDefaultView)[0];
                this.initializeView(defaultView);
            }
            catch(e) {
                this.error = e;
            }
        }).catch((error) => {
            this.error = error;
        });
    }

    private initializeView(view): void {
        this.currentView = view;
        this.initializeLookupDictionary(view.panelElements);
    }

    private initializeLookupDictionary(panelConfig: any[]): void {
        this.lookupDictionary = {};
        panelConfig.forEach(element => {
            this.fillLookupTable(element);
            var xDistance = element.bottomRight.x - element.topLeft.x;
            var yDistance = element.bottomRight.y - element.topLeft.y;
            element.isHorizontal = xDistance > yDistance;
        });
    }

    private fillLookupTable(element): void {
        var topLeftKey = this.getLookupKey(element.topLeft.x, element.topLeft.y);
        for (var x = element.topLeft.x; x <= element.bottomRight.x; x++) {
            for (var y = element.topLeft.y; y <= element.bottomRight.y; y++) {
                var key = this.getLookupKey(x, y);
                if (key === topLeftKey) this.lookupDictionary[key] = element;
                if (key !== topLeftKey) this.lookupDictionary[key] = { type: "occupied" }
            }
        }
    }

    private lookupDictItem(col, row) {
        var key = this.getLookupKey(col, row);
        return this.lookupDictionary[key];
    }

    private getLookupKey(x, y): string {
        var key = x + "," + y;
        return key;
    }

    public counter(i: number): Array<any> {
        var foo = [];
        for (var x = 0; x < i; x++) {
            foo.push(x);
        }
        return foo;
    }

    public buttonPressed(element) {
        try {
            if (element.redirectsToView)
            {
                var filteredViews = this.config.views.filter(x => x.viewId == element.redirectsToView);
                if (filteredViews.count === 0) return;
                var nextView = filteredViews[0];
                this.initializeView(nextView);
            }

            window.location.href = 'panelsapp://' + element.buttonId
        }
        catch(e)
        {
            this.error = e;
        }
    }
}



