import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class ConfigFetcherService {
    constructor(private http: Http) { 

    }

    public getPanelConfig(): Promise<PanelConfig> {
        return new Promise((resolve, reject) => {
            this.http.get("./assets/panel-config.json").subscribe(data => {
                try {
                    resolve(data.json());
                }
                catch(error) {
                    reject(error);
                }
            }, (error) => {
                reject(error);
            });
        })
    }
}

export class PanelConfig {
    public views: PanelView[];
}

export class PanelView {
    public panelElements: PanelElement[];
    public backgroundColor: string;
    public isDefaultView: boolean;
}

export class PanelElement {
    public topLeft: Point;
    public bottomRight: Point;
}

export class Point {
    public x: number;
    public y: number;
}



